body {
  background-color: #ffffff;
  color: #26242e;
  font: 1em sans-serif;
  transition: 0.4s;
}
body ::-moz-selection {
  background-color: #10a37d;
  color: #ffffff;
}
body ::selection {
  background-color: #10a37d;
  color: #ffffff;
}
body.dark-theme {
  background-color: #26242e;
  transition: 0.4s;
  color: #ffffff;
  font: 1em sans-serif;
}
body.dark-theme ::-moz-selection {
  background-color: #4f98d3;
  color: #ffffff;
}
body.dark-theme ::selection {
  background-color: #4f98d3;
  color: #ffffff;
}

